import popupLayer from '../../../../components/popup-layer.vue'
export default {
	data() {
		return {
			// 控制底部弹出框
			shows: false,
			fabu: true,
			result: [],
			results: [],
			classArr: [],
			selectedClassList: [],
			selectedClassNum: ''
		}
	},
	components: {
		popupLayer
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1,
			})
		},
		// 选择单词
		onChanges(event) {
			this.result = event.detail
			console.log(this.result)
		},
		// 选择班级
		onChangess(event) {
			console.log(event)
			this.results = event.detail
			console.log(this.results)
		},
		// 确认选择班级
		confirmClass() {
			this.selectedClassList=[]
			this.results.forEach(data => {
				this.classArr.forEach(val => {
					if (data == val.classId) {
						this.selectedClassList.push(val.className)
					}
				})
			})

			console.log(this.selectedClassList)
			// this.selectedClassList = this.results
			this.selectedClassNum = this.results.length
			this.onClose()
		},
		// 控制---底部弹出框
		onClose() {
			this.shows = false
			this.fabu = true
		},
		showBox() {
			this.shows = true
			this.fabu = false
		},
		// 获取班级下拉列表
		classList() {
			this.$minApi.classList({
				schoolId: 1
			}).then(data => {
				this.classArr = data.data
				console.log(data)
			})
		}
	},
	created() {
		this.classList()
	}
}
